import React, { Component } from 'react'
import { Button } from 'react-bootstrap'
import BootstrapTable from 'react-bootstrap-table-next';
import filterFactory, { textFilter } from 'react-bootstrap-table2-filter';
export default class UserTable extends Component {

    columns = [
        {
            dataField: 'username',
            text: 'Username',
            filter: textFilter(),
            sort: true
        },
        {
            dataField: 'mainCenter',
            text: 'Investigator centre',
            filter: textFilter(),
            sort: true
        },
        {
            dataField: 'userEmail',
            text: 'Email',
            filter: textFilter(),
            sort: true

        },
        {
            dataField: 'lastConnectionDate',
            text: 'Last connection',
            filter: textFilter(),
            sort: true
        },
        {
            dataField: 'userStatus',
            text: 'AccountStatus',
            filter: textFilter(),
            sort: true
        },
        {
            dataField: 'modify',
            text: 'Modify',
            formatter: (cell, row, rowIndex, extraData) => (<Button className='btn btn-secondary' onClick={() => { console.log('button clicked') }}>Modify</Button>
            ),
        }
    ];

    buildRows() {
        let rows = []
        rows[0] = {
            username: 'JaneDoe',
            mainCenter: 'Oncopole',
            userEmail: 'janedoe@whatever.com',
            lastConnectionDate: '08-07-2020',
            userStatus: 'Activated'
        }
        rows[1] = {
            username: 'JohnDoe',
            mainCenter: 'Oncopole',
            userEmail: 'johndoe@whatever.com',
            lastConnectionDate: '08-07-2020',
            userStatus: 'Activated'
        }
        return rows
    }

    render() {
        if (!this.props.hide) return (
            <>
                <BootstrapTable
                    id='tableau_admin'
                    className='table table-striped'
                    keyField='id'
                    data={this.buildRows()}
                    columns={this.columns}
                    filter={filterFactory()}
                >

                </BootstrapTable>
                <p>UserTable</p>
            </>
        )
        return (<> </>)
    }
}