import React, { Component } from 'react'
import { Row, Col, Button, Tabs, Tab } from 'react-bootstrap'
import Select from 'react-select'
import UserTable from './UserTable'

import CreateStudy from './CreateStudy'
import ModifyCenters from './ModifyCenters'
import NewUser from './NewUser'
import StudyActivation from './StudyActivation'
import Preferences from './Preferences'
import TrackerController from './TrackerController'
import { ReactComponent as UploadIcon } from '../../../style/images/upload.svg'

export default class RootAdministrator extends Component {

    state = {
        selectedStudy: undefined,
    }

    /**
    * Update selectedStudy state of panel
    * @param {String} selectedStudy 
    */
    selectStudy = selectedStudy => {
        selectedStudy = selectedStudy.value
        this.setState({ selectedStudy });
    }

    /**
     * Fetch available studies
     * @return {Array}
     */
    fetchAvailableStudies() {
        let visitTypeArray = [{ value: 'allStudies', label: 'All studies' }, { value: 'test', label: 'test' }]
        return visitTypeArray
    }

    render() {
        let userSelector = (<> </>)
        if (!this.props.hideUserSelector) userSelector = (<Row>
            <Col className='text-center'>
                <label>User account database of study:</label>
                <Select className='dropdown d-inline-block' name='study' id='HeaderController' options={this.fetchAvailableStudies()} onChange={this.selectStudy} />
            </Col>
        </Row>)
        return (
            <>
                <Row>
                    <div className='text-right col'>
                        <Preferences />
                        <Button className='btn btn-primary' id='exportDb'
                            onClick={() => console.log('export database')}><UploadIcon /></Button>
                    </div>
                </Row>
                <Row>
                    <div className='text-center col'>
                        <h1>Administration Panel</h1>
                    </div>
                </Row>
                <Col className='text-center'>
                    <Tabs className='admin-tab' >
                        <Tab eventKey='addUser' title='Add User'>
                            <NewUser />
                        </Tab>
                        <Tab eventKey='openModifyCenter' title='Modify center list'>
                            <ModifyCenters />
                        </Tab>
                        <Tab eventKey='openCreateStudy' title='Create Study'>
                            <CreateStudy />
                        </Tab>
                        <Tab eventKey='openStudyActivation' title='Study Activation'>
                            <StudyActivation />
                        </Tab>
                        <Tab eventKey='trackerController' title='Tracker'>
                            <TrackerController />
                        </Tab>
                    </Tabs>
                </Col>
                {userSelector}

            </>
        )
    }
}

