import { ADD_STUDY, ADD_ROLES, SELECT_STUDY, SELECT_ROLE } from './actions-types'

/**
 * Add study to Redux study Object
 * @param {Object} studyObject
 */
export function addStudy (studyObject) {
  return {
    type: ADD_STUDY,
    payload: studyObject
  }
}

/**
 * Add role to Redux role Object
 * @param {Array} roleArray
 */
export function addRoles (roleArray) {
    return {
      type: ADD_ROLES,
      payload: roleArray
    }
  }

/**
 * Update current seleted studyID
 * @param {String} studyID
 */
export function selectStudy (studyID) {
    return {
      type: SELECT_STUDY,
      payload: studyID
    }
  }

/**
 * Update current seleted role
 * @param {String} role
 */
export function selectRole (role) {
    return {
      type: SELECT_ROLE,
      payload: role
    }
  }
