import React, { Component } from 'react'
import { Button, Modal, Form, Row, Col } from 'react-bootstrap'
import { ReactComponent as Gear } from '../../../style/images/gear.svg'

export default class Preferences extends Component {

    state = {
        showPrefs: false,
    }

    constructor(props) {
        super(props)
        this.toggleModal = this.toggleModal.bind(this)
        this.applyChanges = this.applyChanges.bind(this)
    }

    applyChanges = () => {
        //some api call to apply changes
        this.toggleModal()
    }

    toggleModal = () => {
        this.setState((state) => ({ showPrefs: !state.showPrefs }))
    }

    render() {
        return (
            <>
                <Button variant="secondary" className='gear' onClick={this.toggleModal}><Gear /></Button>
                <Modal show={this.state.showPrefs} onHide={this.toggleModal}>
                    <Modal.Header closeButton>
                        <Modal.Title>Platform Preferences</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Form>
                            <Form.Group>
                                <Form.Label className='control-label'>Platform Name</Form.Label>
                                <Form.Control name='plateformName' id='plateformName' type='text' className='form-control' value='' />
                                <Row>
                                    <Col>
                                        <Form.Label className='control-label'>Administrator Email</Form.Label>
                                        <Form.Control name='adminEmail' id='adminEmail' type='text' className='form-control' value='' />
                                    </Col>
                                    <Col>
                                        <Form.Label className='control-label'>Reply to</Form.Label>
                                        <Form.Control name='replyTo' id='replyTo' type='text' className='form-control' value='' />

                                    </Col>
                                </Row>
                                <Form.Label className='control-label'>Corporation</Form.Label>
                                <Form.Control name='corporation' id='corporation' type='text' className='form-control' value='' />
                                <Col>
                                    <Row>
                                        <Form.Label className='control-label'>Patient code length</Form.Label>
                                        <Form.Control inline name='patientCodeLenght' id='patientCodeLenght' type='number' className='form-control' value='14' maxValue='18' />
                                        <Form.Text id="passwordCodeBlock" muted>
                                            Maximum 18.    
                                        </Form.Text>
                                    </Row>
                                </Col>
                                <Col>
                                    <Row>
                                        <Form.Label className='control-label'>Parse Date Import</Form.Label>
                                        <Form.Check type='radio' label='MM/DD/YY' id='parseDateImport1' name='parseDataImport' />
                                        <Form.Check type='radio' label='DD/MM/YY' id='parseDateImport2' name='parseDataImport' />
                                        <Form.Label className='control-label'>Parse Country Name</Form.Label>
                                        <Form.Check type='radio' label='US' id='parseCountryNameUS' name='parseCountryName' />
                                        <Form.Check type='radio' label='FR' id='parseCountryNameFR' name='parseCountryName' />
                                    </Row>
                                </Col>
                            </Form.Group>
                            <Form.Group className='text-center mt-3 mb-3'>
                                <Button className='btn btn-primary' id='updateButton' onClick={this.applyChanges} >Apply</Button>
                            </Form.Group>
                        </Form>
                    </Modal.Body>
                </Modal>
            </>
        )
    }
}