import React, { Component } from 'react'
import Header from '../Header'
import { Card, Form, Button } from 'react-bootstrap'
export default class Request extends Component {

    render() {
        return (
            <>
                <Header />
                <Card className='block block-400'>
                    <Card.Title className='block-title'>Send a request</Card.Title>
                    <Card.Body className='block-content'>
                        <Form className='request-form'>
                            <Form.Group className='form-group'>
                                <Form.Label className='control-label'>Name</Form.Label>
                                <Form.Control className='form-control' type='text' id='name' name='name' required />
                            </Form.Group>
                            <Form.Group className='form-group'>
                                <Form.Label className='control-label'>Email</Form.Label>
                                <Form.Control className='form-control' type='password' id='email' name='email' required />
                            </Form.Group>
                            <Form.Group className='form-group'>
                                <Form.Label className='control-label'>Investigator Center</Form.Label>
                                <Form.Control className='form-control' type='text' id='ic' name='ic' required />
                            </Form.Group>
                            <Form.Group className='form-group'>
                                <Form.Label className='control-label'>Request</Form.Label>
                                <Form.Control className='form-control' as='textarea' id='request' name='request' row='3' required />
                            </Form.Group>
                            <Form.Group>
                                <div className='text-right'>
                                    <Button className='btn btn-secondary' onClick={() => this.props.changeView(undefined)}>Cancel</Button>
                                    <Button className='btn btn-primary' onClick={() => this.props.sendRequest()}>Send</Button>
                                </div>
                            </Form.Group>

                        </Form>
                    </Card.Body>
                </Card>
            </>
        )
    }
}