import { ADD_STUDY, ADD_ROLES, SELECT_STUDY, SELECT_ROLE } from '../actions/actions-types'

const initialState = {
    studies: {},
    roles: [],
    selectedStudy: undefined,
    selectedRole: undefined
}

export default function HeaderReducer(state = initialState, action) {
    switch (action.type) {
        case ADD_STUDY:
            // Add Suty to reducer
            const studyObject = action.payload
            studyObject.numberOfInstances = Object.keys(studyObject.instances).length
            return {
                ...state,
                study: {
                    ...state.study,
                    [studyObject.studyInstanceUID]: { ...studyObject }
                }
            }

        case ADD_ROLES:
            const roleArray = action.payload
            return {
                ...state,
                ...roleArray
            }

        case SELECT_STUDY:
            return {
                ...state,
                selectedStudy: action.payload
            }

        case SELECT_ROLE:
            return {
                ...state,
                selectedRole: action.payload
            }

        default:
            return state
    }
}
