import React, { Component } from 'react'
import { Card, Image, Form, Button } from 'react-bootstrap/'
import logoSquare from '../../style/images/gaelo-logo-square.png'

export default class Authentication extends Component {

    render() {
        return (
            <>
            <div className="splashScreen" ></div>
                <Card className='login block block-500'>
                    <Card.Title className='block-title'>GaelO</Card.Title>
                    <section id='logos'>
                    <Card.Img id='logo-gaelo' src={logoSquare} alt='GaelO' />
                    <div id='name-div'>GaelO</div>
                    </section><Card.Body className='block-content'>
                        <Form className='login-form'>
                            <Form.Group>
                                <Form.Label className='login label'>Username*</Form.Label>
                                <Form.Control className='form-control' type='text' id='username' placeholder='username' name='username' required />
                            </Form.Group>
                            <Form.Group>
                                <Form.Label className='login label'>Password*</Form.Label>
                                <Form.Control className='form-control' type='password' id='mdp' placeholder='password' name='mdp' required />
                            </Form.Group>
                            <Form.Group>
                            <div className='text-right'><Button className='btn btn-dark' onClick={() => this.props.connectUser()}>Connect</Button></div>
                            </Form.Group>
                            <Form.Text>
                                <div className='text-center'>
                                    <div className='href' onClick={() => this.props.changeView('forgotPass')}>Forgot your password?</div>
                                    <div className='href' onClick={() => this.props.changeView('request')}>A request ? Any questions ?</div>
                                </div>
                            </Form.Text>
                        </Form>
                    </Card.Body>
                </Card>
            </>
        )
    }
}