import React, { Component } from 'react'
import Authentication from './Authentication'
import Request from './Request'
import ForgotPassword from './ForgotPassword'

export default class AuthenticationController extends Component {

    state = {
        view: undefined //Designate which window to display
    }

    constructor(props) {
        super(props)
        this.connectUser = this.connectUser.bind(this)
        this.changeView = this.changeView.bind(this)
        this.sendRequest = this.sendRequest.bind(this)
    }

    changeView = (newView) => {
        this.setState({ view: newView })
    }

    sendRequest = (request) => {
        console.log('Request sent!')
        this.changeView(undefined)
    }

    connectUser = () => {
        this.props.isAuthenticated(true)
    }

    render() {
        switch (this.state.view) {
            case 'forgotPass':
                return <ForgotPassword changeView={this.changeView} />
            case 'request':
                return <Request changeView={this.changeView} sendRequest={this.sendRequest}/>
            default:
                return <Authentication connectUser={this.connectUser} changeView={this.changeView} />
        }
    }
}