import React, { Component } from 'react'
import { connect } from 'react-redux'

import { Navbar, Image, Button, Col, Row } from 'react-bootstrap/'
import logoRectangle from '../style/images/gaelo-logo-rectangle.png'
import Select from 'react-select'
import MyAccount from './MyAccount'
import Messenger from './Messenger'
import { ReactComponent as ProfileIcon } from '../style/images/profile.svg'
import { ReactComponent as MessageIcon } from '../style/images/message.svg'
import { ReactComponent as SignoutIcon } from '../style/images/signout.svg'
import { ReactComponent as Gear } from '../style/images/gear.svg'

import { addStudy, addRoles, selectStudy, selectRole } from '../actions/Header'
class Header extends Component {
    state = {
        selectedStudy: undefined, //Selected study ID
        selectedRole: undefined, //Selected role name
        showAccount: false
    }

    constructor(props) {
        super(props)
        this.selectStudy = this.selectStudy.bind(this)
        this.selectRole = this.selectRole.bind(this)
        this.toggleAccount = this.toggleAccount.bind(this)
        this.updateAccount = this.updateAccount.bind(this)
    }

    /**
     * Fetch available studies
     * @return {Array}
     */
    fetchAvailableStudies() {
        let visitTypeArray = [{ value: 'test', label: 'test' }]
        return visitTypeArray
    }

    /**
     * Fetch available Roles
     * @return {Array}
     */
    fetchAvailableRoles() {
        let visitTypeArray = [{ value: 'controller', label: 'Controller' },
        { value: 'investigator', label: 'Investigator' },
        { value: 'monitor', label: 'Monitor' },
        { value: 'reviewer', label: 'Reviewer' },
        { value: 'supervisor', label: 'Supervisor' }]
        return visitTypeArray
    }

    /**
     * Update selectedStudy state of visit
     * @param {String} selectedStudy 
     */
    selectStudy = selectedStudy => {
        selectedStudy = selectedStudy.value
        this.setState({ selectedStudy }, () => {
            if(this.state.selectedRole !== undefined) this.props.triggerNextBody(this.state.selectedStudy, this.state.selectedRole)})
    }

    /**
     * Update selectedRole state and call parent function
     * to generate rows to check
     * @param {String} selectedRole 
     */
    selectRole = selectedRole => {
        selectedRole = selectedRole.value
        this.setState({ selectedRole }, () => {
            if(this.state.selectedStudy !== undefined) this.props.triggerNextBody(this.state.selectedStudy, this.state.selectedRole)})

    }

    toggleAccount = () => {
        this.setState((state) => { return { showAccount: !state.showAccount } })
    }

    updateAccount = () => {
        console.log('Account updated!')
    }
    render() {
        return (
            <>
                <div className='header header-content navbar-static-top'>
                    <Navbar className='header-bg-color'>
                        <Row>
                            <Navbar.Brand>
                                <div id='name-div'>GaelO</div>
                            </Navbar.Brand>
                            <Navbar.Collapse className='navbar-pad'>
                                <Col md='auto'>
                                    <Select placeholder='Study...' className='dropdown inline-d' name='etude' id='etude' options={this.fetchAvailableStudies()} onChange={this.selectStudy} />
                                    <Select placeholder='Role...' className='dropdown' name='role' id='role' options={this.fetchAvailableRoles()} onChange={this.selectRole} />
                                </Col>
                                <Col>
                                    <div className='justify-content-center'>
                                    </div>
                                </Col>
                                <Col md='auto'>
                                    <div className='float-right'>
                                        <Button id="messengerButton" className="btn btn-info"><MessageIcon /></Button>
                                        <Button id="myAccountButton" className="btn btn-info mr-5" onClick={this.toggleAccount}><ProfileIcon /></Button>
                                        <Button id="adminButton" className="btn btn-danger" hidden={this.props.isAdmin} onClick={() => this.props.administratorView()}><Gear /></Button>
                                        <Button id="logOut" className="btn btn-danger" onClick={() => this.props.isAuthenticated(false)}><SignoutIcon /></Button>
                                    </div>
                                </Col>
                                <MyAccount show={this.state.showAccount} closeModal={this.toggleAccount} updateAccount={this.props.updateAccount} />
                            </Navbar.Collapse>
                        </Row>
                    </Navbar>
                </div>
            </>
        )
    }
    //id=
}

const mapStateToProps = state => {
    return {
        studies: state.Header.studies,
        roles: state.Header.roles,
        selectedStudy: state.Header.selectedStudy,
        selectedRole: state.Header.selectedRole
    }
}
const mapDispatchToProps = {
    addStudy,
    addRoles,
    selectStudy,
    selectRole
}

export default connect(mapStateToProps, mapDispatchToProps)(Header)