import React, { Component } from 'react'
import { Card, Form, Button } from 'react-bootstrap'
import Header from '../Header'
import ChangePassword from './ChangePassword'
export default class ForgotPassword extends Component {

    render() {
        return (
            <>
                <Header />
                <Card className='block block-400'>
                    <Card.Title className='block-title'>Password Assistance</Card.Title>
                    <Card.Body className='block-content'>
                        <Form className='login-form'>
                            <Form.Group>
                                <Form.Label className='login label'>Username*</Form.Label>
                                <Form.Control className='form-control' type='text' id='username' placeholder='username' name='username' required />
                            </Form.Group>
                            <Form.Group>
                                <Form.Label className='login label'>Password*</Form.Label>
                                <Form.Control className='form-control' type='password' id='mdp' placeholder='password' name='mdp' required />
                            </Form.Group>
                            <Form.Text>
                                <div className='text-right fset-2'>
                                    <Button className='btn btn-secondary' onClick={() => this.props.changeView(undefined)}>Cancel</Button>
                                    <Button className='btn btn-primary' onClick={() => this.props.sendRequest(undefined)}>Send</Button>
                                </div>
                            </Form.Text>
                        </Form>
                    </Card.Body>
                </Card>
                <ChangePassword />
            </>
        )
    }
}