import React, { Component } from 'react'
import { Modal, Button, Form } from 'react-bootstrap'
export default class MyAccount extends Component {

    render() {
        return (
            <>
                <Modal show={this.props.show} onHide={this.props.closeModal}>
                    <Modal.Header closeButton>
                        <Modal.Title>My account</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Form>
                            <Form.Group>
                                <Form.Label className='control-label'>Username : </Form.Label>
                                <Form.Control name='changeUsername' id='changeUsername' type='text' className='form-control' value='' disabled />
                                <Form.Label className='control-label'>Email : </Form.Label>
                                <Form.Control name='changeEmail' id='changeEmail' type='text' className='form-control' value='' disabled />
                                <Form.Label className='control-label'>Last Name : </Form.Label>
                                <Form.Control name='changeLastName' id='changeLastName' type='text' className='form-control' value='' />
                                <Form.Label className='control-label'>First Name : </Form.Label>
                                <Form.Control name='changeFirstName' id='changeFirstName' type='text' className='form-control' value='' />
                                <Form.Label className='control-label'>Phone Number : </Form.Label>
                                <Form.Control name='changePhoneNumber' id='changePhoneNumber' type='text' className='form-control' value='' />
                            </Form.Group>
                            <Form.Group className='text-center mt-3 mb-3'>
                                <Button className='btn btn-primary' id='updateButton' onClick={this.props.updateAccount} >Update</Button>
                            </Form.Group>
                            <Form.Group>
                                To Change email : Use 'Request' page to contact administrators <br />
                                To Reset Password : Use 'Forgot Password' at main page
                            </Form.Group>
                        </Form>
                    </Modal.Body>
                </Modal>
            </>
        )
    }
}