import React, { Component } from 'react'
import { Card } from 'react-bootstrap/'
import Header from './Header'
import RoleController from './UsersViews/RoleController'
import AuthenticationController from './Authentication/AuthenticationController'
export default class MainView extends Component {

    state = {
        isAuthenticated: false,
        isPanelHidden: true,
        selStudy: undefined,
        selRole: undefined,
    }

    constructor(props) {
        super(props)
        this.triggerNextBody = this.triggerNextBody.bind(this)
        this.isAuthenticated = this.isAuthenticated.bind(this)
        this.administratorView = this.administratorView.bind(this)
    }

    triggerNextBody = (study, role) => {
        this.setState({
            isPanelHidden: false,
            selStudy: study,
            selRole: role
        })
    }

    isAuthenticated = (isAuthenticated) => {
        this.setState({ isAuthenticated: isAuthenticated })
    }

    administratorView = () => {
        this.setState({selRole: 'administrator', isPanelHidden:false})
    }

    render() {
        if (!this.state.isAuthenticated) return (<div className='authentication fill-window'><AuthenticationController isAuthenticated={this.isAuthenticated} /></div>)
        else return (
            <>
                <div className='body fill-window background'>
                    <Header triggerNextBody={this.triggerNextBody} isAuthenticated={this.isAuthenticated} administratorView={this.administratorView} />
                    <Card className='container block block-content card' hidden={this.state.isPanelHidden}>
                        <Card.Body id='nextPanel' className='relative'>
                            <RoleController study={this.state.selStudy} role={this.state.selRole} />
                        </Card.Body>
                    </Card>
                </div>
            </>
        )
    }
}